        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> {{ $store.getters.cartTotalQuantity }} товара </span>
    </div>

    <section class="cart">
      <div v-if="productLoading">
        <img src="Hourglass.gif" alt="Загрузка товара" />
      </div>

      <form
