                name="address"
                placeholder="Введите ваш адрес"
              />
              <span class="form__value">Адрес доставки</span>
              <span v-if="formError.address" class="form__error">
                {{ formError.address }}
              </span>
            </label> -->

            <BaseFormText
              v-model="formData.phone"
              :error="formError.phone"
              type="tel"
              title="Телефон"
              placeholder="Введите ваш телефон"
            />

            <!-- <label class="form__label">
              <input
                v-model="formData.phone"
                class="form__input"
                type="tel"
                name="phone"
                placeholder="Введите ваш телефон"
              />
              <span class="form__value">Телефон</span>
              <span v-if="formError.phone" class="form__error">
                {{ formError.phone }}
              </span>
            </label> -->

            <BaseFormText
              v-model="formData.email"
              :error="formError.email"
              type="email"
              title="Email"
              placeholder="Введи ваш Email"
            />

            <!-- <label class="form__label">
              <input
                v-model="formData.email"
                class="form__input"
                type="email"
                name="email"
                placeholder="Введи ваш Email"
              />
